.location {

    &__header {
        background: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), url('../img/tokyodowntown.jpg');
        background-size: cover;
        background-position: top;
        height:80vh;
        padding: 4rem 0 8rem 8rem;
    }
    &__title {
        background-color: white;
        display: inline-block;
        text-transform: uppercase;
        font-size: 6rem;
        letter-spacing: 1rem;
        padding: 0rem 2rem;
    }
    &__hours {
        background-color: white;
        display: inline-block;
        margin-top: 4rem;
        padding: 1rem 2rem;
    }
    &__day {
        font-weight: 500;
        font-size: 2rem;
    }
    &__time {
        font-size: 2rem;
        margin-bottom: 1rem;
    }
    &__listing {
        margin: 0 10%;
        &--image {
            width: 100%;
        }
        &--1 {
            padding: 12rem;
            &-title{
                font-size: 3.4rem;
                text-transform: uppercase;
            }
            &-address{
                padding-bottom:2rem;
                font-weight: 500;
            }
        }
        &--2 {
            padding: 0 12rem;
            padding-bottom: 12rem;
            &-title{
                font-size: 3.4rem;
                text-transform: uppercase;
            }
            &-address{
                padding-bottom:2rem;
                font-weight: 500;
            }
        }
    }
   
}